import { Component, OnInit, OnDestroy } from '@angular/core';
import { AppState } from '../store/app.state';
import { Store } from '@ngrx/store';
import { getPostsRequest } from '../store/actions/posts.actions';
import { MatCalendarCellCssClasses } from '@angular/material';
import { Subscription } from 'rxjs';
import { getSongsReleaseDates } from '../store/reducers/posts.reducer';

@Component({
  selector: 'app-calendar',
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.component.scss']
})
export class CalendarComponent implements OnInit, OnDestroy {

  // title = 'ng-calendar-demo';
  selectedDate = new Date();
  startAt = new Date('2019/09/11');
  minDate = new Date('2019/09/14');
  maxDate = new Date(new Date().setMonth(new Date().getMonth() + 1));
  year: any;
  DayAndDate: string;

  releaseDates = [];

  subscriptions = new Subscription();

  constructor(private store: Store<AppState>) {
    this.onSelect(this.selectedDate);
    this.store.dispatch(getPostsRequest());
  }

  ngOnInit() {
    this.subscriptions.add(this.store.select(getSongsReleaseDates).subscribe(dates => this.releaseDates = dates));
  }

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }

  onSelect(event) {
    console.log(event);
    this.selectedDate = event;
    const dateString = event.toDateString();
    const dateValue = dateString.split(' ');
    this.year = dateValue[3];
    this.DayAndDate = dateValue[0] + ',' + ' ' + dateValue[1] + ' ' + dateValue[2];
  }

  getClass() {
    return (date: Date): MatCalendarCellCssClasses => {
      this.releaseDates.map(dt => new Date(dt))
        .some(d => d.getDate() === date.getDate()
          && d.getMonth() === date.getMonth()
          && d.getFullYear() === date.getFullYear());
      return this.releaseDates ? '.has-releases' : '';

    };
  }

}
